<template>
<!--指标数据 -->
  <el-row>
      <el-col :span="24">
        <div class="stat__header">
          <span class="fl">指标数据</span>
        </div>
        <div class="stat__body">
          <ul class="flex-card">
            <li class="col-20">
              <div class="index__card card-1">
                <div class="card__logo">
                  <img src="./../../assets/images/index/i-1.png" alt srcset>
                </div>
                <div class="card__body">
                  <p class="card-name">会员数</p>
                  <count-to :endVal="data.member" class="card-count"></count-to>
                  <p class="card-total">累积:{{data.memberSum}}</p>
                </div>
              </div>
            </li>

            <li class="col-20">
              <div class="index__card card-2">
                <div class="card__logo">
                  <img src="./../../assets/images/index/i-2.png" alt srcset>
                </div>
                <div class="card__body">
                  <p class="card-name">商家数</p>
                  <count-to :endVal="data.store" class="card-count"></count-to>
                  <p class="card-total">累积:{{data.storeSum}}</p>
                </div>
              </div>
            </li>

            <li class="col-20">
              <div class="index__card card-3">
                <div class="card__logo">
                  <img src="./../../assets/images/index/i-3.png" alt srcset>
                </div>
                <div class="card__body">
                  <p class="card-name">商品数</p>
                  <count-to :endVal="data.product" class="card-count"></count-to>
                  <p class="card-total">累积:{{data.productSum}}</p>
                </div>
              </div>
            </li>
            <li class="col-20">
              <div class="index__card card-4">
                <div class="card__logo">
                  <img src="./../../assets/images/index/i-4.png" alt srcset>
                </div>
                <div class="card__body">
                  <p class="card-name">订单数</p>
                  <count-to :endVal="data.order" class="card-count"></count-to>
                  <p class="card-total">累积:{{data.orderSum}}</p>
                </div>
              </div>
            </li>
            <li class="col-20">
              <div class="index__card card-5">
                <div class="card__logo">
                  <img src="./../../assets/images/index/i-6.png" alt srcset>
                </div>
                <div class="card__body">
                  <p class="card-name">售后订单数</p>
                  <count-to :endVal="data.refund" class="card-count"></count-to>
                  <p class="card-total">累积:{{data.refundSum}}</p>
                </div>
              </div>
            </li>
          </ul>
        </div>
      </el-col>
    </el-row>
</template>

<script>
import { commandTarget } from './../../api/index/index.js'
const defaultData = {
  member: 0,
  memberSum: 0,
  order: 0,
  orderSum: 0,
  product: 0,
  productSum: 0,
  refund: 0,
  refundSum: 0,
  store: 0,
  storeSum: 0
}
export default {
  data() {
    return {
      data: Object.assign({}, defaultData)
    }
  },
  methods: {
    getData() {
      commandTarget().then(res => {
        this.data = res.data
      })
    }
  },
  created() {
    this.getData()
  }
}
</script>

 <style lang="scss" scoped>

.flex-card {
  margin: 0 -10px 0 -10px;
  &::before,
  ::after {
    content: "";
    display: table;
  }
}
.col-20 {
  box-sizing: border-box;
  float: left;
  width: 20%;
  padding: 0 10px;
}
.index__card {
  background: #5ebde7;
  overflow: hidden;
}
.card__logo {
  padding: 24px 15px;

  float: left;
  width: 75px;
  height: 100px;
  img {
    width: 36px;
    height: 38px;
  }
}
.card__body {
  margin-left: 75px;
  height: 100px;
  color: #fff;
  padding: 10px 20px;
}
.card-name {
  font-size: 16px;
}
.card-count {
  display: block;
  margin: 3px 0;
  font-size: 26px;
}
.card-total {
  font-size: 14px;
}

.card-1 {
  .card__logo {
    background: #5ebde7;
  }
  .card__body {
    background: #8cd0ee;
  }
}

.card-2 {
  .card__logo {
    background: #afb5ea;
  }
  .card__body {
    background: #c6caf0;
  }
}
.card-3 {
  .card__logo {
    background: #7b84a3;
  }
  .card__body {
    background: #a1a7bd;
  }
}
.card-4 {
  .card__logo {
    background: #50d1d2;
  }
  .card__body {
    background: #82d0df;
  }
}
.card-5 {
  .card__logo {
    background: #3d9de7;
  }
  .card__body {
    background: #74b9ee;
  }
}
</style>



