<template>
  <!-- 流量看板数据 -->
  <div>
    <div class="stat__header">
      <span class="fl">流量数据</span>
      <div class="fr">
        <span class="h-tab"
        :style="active===1?{'color':color}:{}"
        @click="changeData(1)"
        >今日数据</span>
        <span class="h-tab"
         @click="changeData(2)"
         :style="active===2?{'color':color}:{}"
        >七日数据</span>
      </div>
    </div>
    <div class="stat__body" v-loading="loading">
      <div id="proLineChart" class="flow-chart"></div>
    </div>
  </div>
</template>
<script>
import { commandFluxData } from './../../api/index/index.js'
import echarts from 'echarts'
export default {
  data() {
    return {
      chart: null, // 图表实例
      loading: false,
      el: 'proLineChart',
      optionsData: {
        tooltip: {
          trigger: 'axis'
        },
        grid: {
          top: 30,
          left: 10,
          right: 10,
          bottom: 0,
          containLabel: true
        },
        xAxis: {
          type: 'category',
          boundaryGap: true,
          splitLine: {
            show: false,
            lineStyle: {
              type: 'dashed'
            }
          },
          data: ['0-0']
        },
        yAxis: [
          {
            name: '访问量',

            splitLine: {
              show: false
            },
            type: 'value'
          },
          {
            name: '访客数',

            splitLine: {
              show: false
            },
            type: 'value'

          }
        ],
        series: [
          {
            name: '访问量',
            type: 'line',
            smooth: true,
            data: [0]
          },
          {
            name: '访客数',
            type: 'line',
            yAxisIndex: 1,
            smooth: true,
            data: [0]
          }
        ]
      },
      weekData: [],
      dayData: [],
      active: 1
    }
  },
  computed: {
    color() {
      return this.$store.state.system.color
    }
  },
  methods: {
    changeData(index) {
      if (index === 1) {
        this.optionsData.xAxis.data = this.dayData.xAxis
        this.optionsData.series[0].data = this.dayData.yVisit
        this.optionsData.series[1].data = this.dayData.yMember
      } else {
        this.optionsData.xAxis.data = this.weekData.xAxis
        this.optionsData.series[0].data = this.weekData.yVisitWeek
        this.optionsData.series[1].data = this.weekData.yMemberWeek
      }
      this.active = index
      this.chart.setOption(this.optionsData)
    },
    getData() { // 只要复写这个函数就可以操作对应的图表数据,
      commandFluxData().then(res => {
        this.dayData = res.data.option
        this.weekData = res.data.optionWeek
        this.changeData(1)
      })
    },
    initChart() {
      // 初始化图表
      this.$nextTick(() => {
        this.chart = echarts.init(document.getElementById(this.el), 'walden')
      })
    }
  },
  created() {
    this.getData()
  },
  mounted() {
    // 监听图表
    this.initChart()
    window.addEventListener('resize', () => {
      this.chart.resize()
    })
  }
}
</script>

<style>
.flow-chart {
  width: 99%;
  height: 300px;
}
.h-tab{
  cursor: pointer;
  margin-left: 10px;
}
</style>



