<template>
  <div>
    <el-row class="mt-20" :gutter="20">
      <el-col :span="14">
        <div class="stat__header">
          <span class="fl">待办事务</span>
        </div>
        <div class="stat__body" style="height:130px;">
          <ul class="index-counts">
            <li class="index-count">
              <count-to :endVal="tData.store" class="index-val"></count-to>
              <p>商家入驻</p>
            </li>
            <li class="index-count">
              <count-to :endVal="tData.product" class="index-val"></count-to>
              <p>商品审核</p>
            </li>
            <li class="index-count">
              <count-to :endVal="tData.refund" class="index-val"></count-to>
              <p>售后订单</p>
            </li>
            <li class="index-count">
              <count-to :endVal="tData.settlement" class="index-val"></count-to>
              <p>商家结算</p>
            </li>
            <li class="index-count">
              <count-to :endVal="tData.report" class="index-val"></count-to>
              <p>投诉管理</p>
            </li>
          </ul>
        </div>
      </el-col>
      <el-col :span="10">
        <div class="stat__header">
          <span class="fl">平台营销活动</span>
        </div>
        <div class="stat__body items-body" style="height:130px;">
          <ul>
            <template v-for="(item,index) in remoteData">
              <li class="index-item" v-if="index<4" :key="index">
                <a  @click="$routerLink('/activity/list','list')">
                  {{item.promoteBeginTime}}&nbsp;&nbsp;&nbsp;
                  <span style="max-width:60%;vertical-align: top" class="inlineBlock text-overflow">{{item.name}}</span>
                  <el-tag class="fr" size="mini" :type="item.status===1?'':(item.status===2?'success':'danger')">
                    {{item.status===1?'未开始':(item.status===2?'进行中':'已结束')}}</el-tag>
                </a>
              </li>
            </template>
          </ul>
        </div>
      </el-col>
    </el-row>
    <el-row class="mt-20">
      <el-col :span="24">
        <div class="stat__header">
          <span class="fl">营销活动使用情况</span>
        </div>
        <div class="stat__body table-body">
          <el-table :data="userData"  class="index-table"
          :header-cell-style="{'background':'#fff'}"
          :cell-style="{'border':'none'}"
          >
            <el-table-column label="序号" >
              <template slot-scope="scope">
                {{scope.$index+1}}
              </template>
            </el-table-column>
            <el-table-column label="活动名称" prop="name">
              <template slot-scope="scope"> 
                <a @click="$routerLink('/activity/list','list')">{{scope.row.name}}</a>
              </template>
            </el-table-column>
            <el-table-column label="订单总数" prop="orderNum"></el-table-column>
            <el-table-column label="订单总成交额" prop="orderMoney"></el-table-column>
            <el-table-column label="拉新数" prop="newUser"></el-table-column>
          </el-table>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { commandTrancation, commandActivity, commandActivityUser } from './../../api/index/index.js'
export default {
  data() {
    return {
      tData: {
        store: 0,
        product: 0,
        refund: 0,
        settlement: 0,
        report: 0
      },
      remoteData: [],
      userData: []
    }
  },
  methods: {
    getTrancationData() {
      commandTrancation().then(res => {
        this.tData = res.data
      })
      commandActivity().then(res => {
        this.remoteData = res.data
      })
      commandActivityUser().then(res => {
        this.userData = res.data.slice(0, 5)
      })
    }
  },
  created() {
    this.getTrancationData()
  }
}
</script>
<style lang="scss" scoped>
.index-counts {
  overflow: hidden;
}
.index-count {
  float: left;
  width: 20%;
  text-align: center;
  margin-top: 15px;
  font-size: 16px;
  cursor: pointer;
  p {
    font-size: 16px;
    color: #686868;
    margin: 3px 0;
  }
}
.index-val {
  color: #79a9f2;
  font-size: 30px;
  font-weight: 500;
}
.index-item {
  font-size: 14px;
  color: #666;
  line-height: 24px;
  max-width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.items-body {
  padding: 10px 30px;
}
.table-body{
  padding: 15px 20px;
  .el-table{
    border: none;
  }
}
.el-table::before{
  height: 0;
}
</style>




