<template>
  <!-- 流量看板数据 -->
  <div>
    <div class="stat__header">
      <span class="fl">页面数据</span>
      <div class="fr">
        <span class="h-tab"
        :style="active===1?{'color':color}:{}"
        @click="changeData(1)"
        >今日数据</span>
        <span class="h-tab"
         @click="changeData(2)"
         :style="active===2?{'color':color}:{}"
        >七日数据</span>
      </div>
    </div>
    <div class="stat__body" v-loading="loading">
      <div id="pieChart" class="flow-chart"></div>
    </div>
  </div>
</template>
<script>
import { commandPageData } from './../../api/index/index.js'
import echarts from 'echarts'
export default {
  data() {
    return {
      chart: null, // 图表实例
      loading: false,
      el: 'pieChart',
      optionsData: {
        // color: ['#C23531', '#2F4554', '61A0A8', '#D48265', '#91C7AE', '#749F83', '#CA8622', '#BDA29A', '#6E7074', '#546570'],
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b}: {c} ({d}%)'
        },
        series: [
          {
            name: '访问人数',
            type: 'pie',
            radius: [0, '40%'],
            label: {
              normal: {
                position: 'inner'
              }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            data: []
          },
          {
            name: '访问记录',
            type: 'pie',
            radius: ['55%', '75%'],
            data: []
          }
        ]
      },
      weekData: {
        people: [],
        record: []
      },
      data: {},
      active: 1
    }
  },
  computed: {
    color() {
      return this.$store.state.system.color
    }
  },
  methods: {
    changeData(index) {
      if (index === 1) {
        this.optionsData.series[0].data = this.data.people
        this.optionsData.series[1].data = this.data.record
      } else {
        this.optionsData.series[0].data = this.weekData.people
        this.optionsData.series[1].data = this.weekData.record
      }
      this.active = index
      this.chart.setOption(this.optionsData)
    },
    getData() { // 只要复写这个函数就可以操作对应的图表数据,
      commandPageData().then(res => {
        this.data = res.data
        res.data.people.forEach(v => {
          const obj = {
            name: v.name,
            value: v.valueWeek
          }
          this.weekData.people.push(obj)
        })
        res.data.record.forEach(v => {
          const obj = {
            name: v.name,
            value: v.valueWeek
          }
          this.weekData.record.push(obj)
        })
        this.changeData(1)
      })
    },
    initChart() {
      // 初始化图表
      this.$nextTick(() => {
        this.chart = echarts.init(document.getElementById(this.el), 'walden')
      })
    }
  },
  created() {
    this.getData()
  },
  mounted() {
    // 监听图表
    this.initChart()
    window.addEventListener('resize', () => {
      this.chart.resize()
    })
  }
}
</script>

<style>
.flow-chart {
  width: 100%;
  height: 300px;
}
.h-tab{
  cursor: pointer;
  margin-left: 10px;
}
</style>



